.categories {
  &__wrapper {
    display: grid;
    row-gap: 40px;

    @include onTablet {
      row-gap: 56px;
    }
  }

  .button__wrapper {
    @include pageGrid;

    width: 100%;
  }

  &__button {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 56px;
    border-radius: 8px;
    border-width: 0;
    background-color: #f98921;
    color: #fff;
    font-size: 16px;
    line-height: 27px;
    font-weight: $font-bold;
    grid-column: 1 / -1;

    @include onDesktop {
      grid-column: span 4;
    }
  }

  &__button:hover {
    background-color: #1b2129;
    cursor: pointer;
  }
  &__button:active {
    background-color: #010810;
  }

  .category {
    &__photos {
      @include pageGrid;

      margin-bottom: 16px;
    }

    &__link {
      margin: 0;
      position: relative;
      height: 0;
      padding-bottom: 100%;

      @include onTablet {
        grid-column: span 3;
        height: auto;
        padding-bottom: 0;
      }

      @include onDesktop {
        grid-column: span 6;
        height: auto;
        padding-bottom: 0;
      }
    }

    &__photo {
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;

      @include onTablet {
        position: relative;
        height: auto;
      }

      @include onDesktop {
        position: relative;
        height: auto;
      }
    }

    &__link:hover {
      transform: scale(1.1);
    }

    &__title {
      font-size: $font-size-product-name;

      font-weight: 700;
      @include onTablet {
        font-size: 24px;
      }
    }
  }
}
