.main:not(:last-child) {
  background-color: #fff;
  display: grid;
  row-gap: 96px;

  &:first-child {
    padding-top: 64px;
  }

  @include onTablet {
    row-gap: 96px;
  }

  @include onDesktop {
    row-gap: 120px;
  }
}

.main__image {
  display: flex;
  height: fit-content;
}

.image__photo {
  width: 100%;
  height: 200px;
  object-fit: cover;
  margin: 0;

  @include onTablet {
    height: 400px;
  }

  @include onDesktop {
    height: 460px;
  }
}

.about-us {
  display: flex;
  height: fit-content;
  box-sizing: border-box;
  margin-top: -120px;
  background-color: #fcfaef;

  @include onTablet {
    height: 416px;
  }

  @include onDesktop {
    height: 350px;
  }
}

.footer-wrapper {
  @include pageGrid;

  .section-title {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: span 6;
    }
  }

  .section__description {
    letter-spacing: 0.15px;
    margin: 0;

    padding-top: 24px;
    padding-bottom: 48px;
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: span 3;
      padding-top: 120px;
      padding-bottom: 120px;
    }

    @include onDesktop {
      grid-column: 8 / -1;
    }
  }
}
